import { Component, Input, OnChanges } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from "rxjs";
import { Project } from '../class/project';
import { Session } from '../class/session';


@Component({
  selector: 'map-event',
  templateUrl: './map-event.component.html',
  styleUrls: ['./map-event.component.css']
})

export class MapEventComponent implements OnChanges {
	@Input() project: Project;
	lat: number = 0;
  	lng: number = 0;
  	zoom: number = 8;
  	nbValDisplay: number = 4;
  	first: number = 0;
  	last: number = this.nbValDisplay - 1;
  	max: number;
  	load: boolean = false;
  	finish: boolean = false;
  	allCoordinates: Session[] = [];
  	coordinates: Session[] = [];
  	maxZindex: number = 1;
  	url: string;
  	lastDate: string;
  	alreadyReplay: boolean = false;

	constructor(private database: AngularFireDatabase){}

	ngOnChanges(){
		if(this.project !== undefined){
			this.first = 0;
			this.last = this.nbValDisplay - 1;
			this.allCoordinates = [];
			this.coordinates = [];
			if(this.project.name === "Global")
				this.url = "/sessions";
			else{
				this.url = "/project-sessions/" + this.project.id;
			}
			this.drawMap(0);
		}
	}

	onScroll(e: any): any{
		let size: number = e.target.clientHeight;
		let sizeOneItem: number = size / this.nbValDisplay;
		this.first = Math.round(e.target.scrollTop / sizeOneItem);
		this.last = this.first + this.nbValDisplay - 1;
		console.log(this.first, this.last, this.max - 1);
		this.changeValue();
		if(this.last >= this.max - 2 && !this.load && !this.finish){
			this.load = true;
			this.loadOtherValue();
		}
	}

	drawMap(index: number): void{
		this.load = true;
		this.database.list(this.url, {
	      	query: {
	        orderByChild: 'serverSessionStartTime',
	        limitToLast: 150
	    }
	    }).subscribe(data => {
	    	let val: Session[] = [];
	    	let first: boolean = true;
    		let newData: boolean = false;
      		data.forEach(item => {
	      		let newCoord = new Session();
	      		if(item.serverSessionStartTime === undefined){
	      			this.finish = true;
	      			console.log(this.finish);
	      		}
	      		else if(item.latitude != '' && item.latitude != undefined &&
	      			item.longitude != '' && item.longitude != undefined){
	      			newCoord.latitude = parseFloat(item.latitude);
	      			newCoord.longitude = parseFloat(item.longitude);
	      			newCoord.callout = item.callout;
	      			newCoord.iconUrl += this.putIconUrl(item.callout);
	      			newCoord.startTime = item.serverSessionStartTime;
	      			newCoord.metadata = item.metadata;
	      			newCoord.userId = this.putUserId(item.userId);
	      			newCoord.moduleId = item.moduleId;
	      			newCoord.zIndex = 1;
	      			if(newData === false){
		      			this.lat = newCoord.latitude;
			      		this.lng = newCoord.longitude;
			      		newData = true;
			      	}
			      	val.push(newCoord)
      			}
      			if(first){
      				this.lastDate = item.serverSessionStartTime;
      				first = false;
      			}
      		});
      		val.reverse();
      		val.forEach(item => {
      			this.allCoordinates.push(item);
      		});
      		this.max = val.length;
      		if(this.max <= this.nbValDisplay){
      			this.loadOtherValue();
      		}
      		else{
      			this.changeValue();
      			this.load = false;
      		}
	    });
	}

	// leftMove(): void{
	// 	if(this.first !== 0){
	// 		this.first --;
	// 		this.last --;
	// 		this.changeValue();
	// 	}
	// }

	// rightMove(): void{
	// 	if(this.load === false){
	// 		this.first ++;
	// 		this.last ++;
	// 		console.log(this.first, this.last, this.max);
	// 		if(this.last >= this.max){
	// 			this.load = true;
	// 			this.loadOtherValue();
	// 		}
	// 		else{
	// 			this.changeValue();
	// 		}
	// 	}
	// }

	zoomOnElement(item: Session){
		this.lat = item.latitude;
		this.lng = item.longitude;
		this.zoom = 16;
		this.maxZindex ++;
		item.zIndex = this.maxZindex;
	}

	changeValue(): void{
		let j: number = 0;
		let maxLat: number = -1000;
		let maxLng: number = -1000;
		let minLat: number = 1000;
		let minLng: number = 1000;
		for(let i = this.first; i <= this.last; i++){
			if(this.allCoordinates[i] !== undefined){
				if(this.allCoordinates[i].latitude > maxLat)
					maxLat = this.allCoordinates[i].latitude;
				if(this.allCoordinates[i].latitude < minLat)
					minLat = this.allCoordinates[i].latitude;
				if(this.allCoordinates[i].longitude > maxLng)
					maxLng = this.allCoordinates[i].longitude;
				if(this.allCoordinates[i].longitude < minLng)
					minLng = this.allCoordinates[i].longitude;
				this.coordinates[j] = this.allCoordinates[i];
				j ++;
			}
		}
		this.lng = (minLng + maxLng) / 2;
		this.lat = (maxLat + minLat) / 2;
		let max: number = Math.max(Math.abs(this.lng - minLng), Math.abs(this.lng - maxLng),
								   Math.abs(this.lat - minLat), Math.abs(this.lat - maxLat));
		this.zoom = this.calculateZoom(max);
	}

	calculateZoom(max: number): number{
		if(max <= 0.001)
			return 15;
		else if(max <= 0.005)
			return 13;
		else if(max <= 0.02)
			return 12;
		else if(max <= 0.1)
			return 11;
		else if(max <= 0.5)
			return 7;
		else if(max <= 1)
			return 6;
		else if(max <= 2)
			return 5;
		else if(max <= 50)
			return 2;
		else
			return 1;
	}

	loadOtherValue(): void{
		this.load = true;
		this.database.list(this.url, {
	    	query: {
		        orderByChild: 'serverSessionStartTime',
		        endAt: this.lastDate,//this.allCoordinates[this.max - 1].startTime,
		        limitToLast: this.nbValDisplay * 5
	      	},
	    }).subscribe(data => {
	    	console.log(data);
	    	let first: boolean = true;
	    	let i: number = 0;
	    	let val: Session[] = [];
		    data.forEach(item => {
		   		let newCoord = new Session();
		   		if(first){
		   			this.lastDate = item.serverSessionStartTime;
		   			first = false;
		   		}
		   			if(item.serverSessionStartTime === undefined){
	      				this.finish = true;
	      			}
		   			else if(i != this.nbValDisplay &&
		   					item.latitude != '' && item.latitude != undefined &&
	      					item.longitude != '' && item.longitude != undefined){
						newCoord.latitude = parseFloat(item.latitude);
			      		newCoord.longitude = parseFloat(item.longitude);
			      		newCoord.callout = item.callout;
			      		newCoord.iconUrl += this.putIconUrl(item.callout);
			      		newCoord.startTime = item.serverSessionStartTime;
			      		newCoord.metadata = item.metadata;
	      				newCoord.userId = this.putUserId(item.userId);
	      				newCoord.moduleId = item.moduleId;
	      				newCoord.zIndex = 1;
			      		val.push(newCoord);
			      	}
			      	i++;
    		});
    		if(val.length == 0 && !this.finish){
    			this.loadOtherValue();
    		}
    		else{
	    		val.reverse();
	    		val.forEach(item => {
	    			this.allCoordinates.push(item);
	    		});
	    		this.max += val.length;
	    		this.changeValue();
	    		this.load = false;
	    	}
		});
	}

	putIconUrl(val: string): string{
		switch (val) {
			case "IDENTIFY":
				return "iconIdentify.png";
			case "REGISTER":
				return "iconRegister.png";
			case "VERIFY":
				return "iconVerify.png";
			case "UPDATE":
				return "iconUpdate.png";
		}
	}

	putUserId(val: string): string{
		if(val.length > 20){
			return val.substring(0, 20) + " " + val.substring(20);
		}
		else
			return val;
	}

	calculateGlobalZoom(): void{
		let maxLat: number = -1000;
		let maxLng: number = -1000;
		let minLat: number = 1000;
		let minLng: number = 1000;
		for(let i = 0; i <= this.coordinates.length; i++){
			if(this.coordinates[i] !== undefined){
				if(this.coordinates[i].latitude > maxLat)
					maxLat = this.coordinates[i].latitude;
				if(this.coordinates[i].latitude < minLat)
					minLat = this.coordinates[i].latitude;
				if(this.coordinates[i].longitude > maxLng)
					maxLng = this.coordinates[i].longitude;
				if(this.coordinates[i].longitude < minLng)
					minLng = this.coordinates[i].longitude;
			}
		}
		this.lng = (minLng + maxLng) / 2;
		this.lat = (maxLat + minLat) / 2;
		let max: number = Math.max(Math.abs(this.lng - minLng), Math.abs(this.lng - maxLng),
								   Math.abs(this.lat - minLat), Math.abs(this.lat - maxLat));
		this.zoom = this.calculateZoom(max);
	}

	replayEvent(nb: number): void{
		console.log(this.allCoordinates.length, this.finish);
		while(this.allCoordinates.length < nb && this.finish != true){
			if(!this.load)
				this.loadOtherValue();
		}
		this.coordinates.length = 1;
		this.alreadyReplay = true;
		let n = Math.min(nb, this.allCoordinates.length);
		let i: number = 0;
		let timer = Observable.timer(0, 700);
		let subscribe = timer.subscribe(t => {
			this.coordinates.push(this.allCoordinates[i]);
			this.calculateGlobalZoom();
			if(t == n - 1){
				subscribe.unsubscribe();
				this.coordinates.length = this.nbValDisplay;
				this.alreadyReplay = false;
				this.first = n - this.nbValDisplay;
				this.last = this.first + this.nbValDisplay - 1;
				this.changeValue();
			}
			i++;
		});
	}
}







