import { Component, Input, OnChanges } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Project } from '../class/project';
import { Utils } from '../class/utils';
import { isUndefined } from "util";

@Component({
  selector: 'user-info',
  templateUrl: './user-info.component.html',
  styleUrls: ['./user-info.component.css']
})

export class UserInfoComponent implements OnChanges{
  @Input() project: Project;
  appVersionList: string[];
  hardwareVersionList: string[];
  appVersionObject: any = {};
  hardwareVersionObject: any = {};
  load: boolean = false;

  constructor(private database: AngularFireDatabase) {}

  ngOnChanges(): void {
    this.appVersionList = [];
    this.hardwareVersionList = [];
    this.appVersionObject = {};
    this.hardwareVersionObject = {};
    let url: string;
    this.load = false;
    if(this.project.name !== 'Global')
      url = `dashboard/project/${this.project.id}/project-data`;
    else
      url = 'dashboard/global';
    this.database.list(`${url}/info`).subscribe(data => {
      data.forEach(item => {
        if(item.$key === 'appVersion')
          this.appVersionObject = item;
        if(item.$key === 'hardwareVersion')
          this.hardwareVersionObject = item;
      });
      for(let key in this.appVersionObject)
        this.appVersionList.push(key);
      this.appVersionList.sort((a, b) => this.sortDecreasing(a, b));
      for(let key in this.hardwareVersionObject)
        this.hardwareVersionList.push(key);
      this.hardwareVersionList.sort((a, b) => this.sortDecreasing(a, b));
      this.load = true;
    });
  }

  sortDecreasing(a: string, b: string): number {
    if(a < b)
      return 1;
    else if(a > b)
      return -1;
    else
      return 0;
  }
}



