import { Component, Input, OnInit}  from '@angular/core';
import { AngularFireDatabase }         from 'angularfire2/database';
import { FirebaseObjectObservable }    from 'angularfire2/database';
import { Project }                     from './../class/project';
import { DragulaService }              from 'ng2-dragula/ng2-dragula';

@Component({
  selector: 'change-config-admin',
  templateUrl: './change-config-admin.component.html',
  styleUrls: ['./change-config-admin.component.css']
})

export class ChangeConfigAdminComponent implements OnInit {
	@Input() project: Project;
	msg: string = '';
  	items: string[] = [];

  	constructor(private dragula: DragulaService, private database: AngularFireDatabase) { }

  	ngOnInit() {
    	this.database.list("/config/definition")
    	.subscribe(data => {
    		data.forEach(item => {
    			this.items[item.index] = item.$key;
    		})
    	//this.items.sort((a, b))
    	})
  	}

  	changeOrder(){
  		let i: number = 0;
  		let value: FirebaseObjectObservable<any>;
  		this.items.forEach(item => {
  			value = this.database.object("/config/definition/" + item);
  			value.update({index: i});
  			i ++;
  		});
  		this.msg = "The change is good";
  	}
}



