import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { ChartsModule } from 'ng2-charts/ng2-charts';
import { Ng2SmartTableModule } from 'ng2-smart-table';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { DashboardRoutingModule } from './dashboard.routing';
import { DashboardComponent } from './dashboard.component';
import { ChartComponent } from '../chart/chart.component';
import { DragDropDirective } from '../directive/drag-drop.directive';
import { TableComponent } from '../table/table.component';
import { MapComponent } from '../map/map.component';
import { MapEventComponent } from '../map_event/map-event.component';
import { AgmCoreModule } from '@agm/core';
import { Ng2PageScrollModule } from 'ng2-page-scroll';
import { AuthGuard } from '../auth-guard.service';
import { AuthService } from '../auth.service';
import { ChangeConfigComponent } from '../change_config/change-config.component';
import { NglModule } from 'ng-lightning/ng-lightning';
import { ChangeConfigAdminComponent } from '../change_config_admin/change-config-admin.component';
import { DragulaModule } from 'ng2-dragula/ng2-dragula';
import { CreateProjectComponent } from '../create_project/create-project.component';
import { GeneralInformationComponent } from '../general-info/general-information.component';
import { ProjectImpactComponent } from '../project-impact/project-impact.component';
import { MultipleSelectComponent } from '../multiple-select/multiple-select.component';
import { ProjectErrorComponent } from '../project-error/project-error.component';
import { ProjectPerformanceComponent } from '../project-performance/project-performance.component';
import { UserInfoComponent } from '../user-info/user-info.component';


@NgModule({
  declarations: [
    DashboardComponent,
    ChartComponent,
    DragDropDirective,
    TableComponent,
    MapComponent,
    MapEventComponent,
    ChangeConfigComponent,
    ChangeConfigAdminComponent,
    CreateProjectComponent,
    GeneralInformationComponent,
    ProjectImpactComponent,
    MultipleSelectComponent,
    ProjectErrorComponent,
    ProjectPerformanceComponent,
    UserInfoComponent
  ],
  imports: [
    CommonModule,
    DashboardRoutingModule,
    ChartsModule,
    AgmCoreModule,
    FormsModule,
    Ng2PageScrollModule,
    Ng2SmartTableModule,
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    NglModule,
    DragulaModule
  ],
  providers: [
    AuthGuard,
    AuthService
  ]
})
export class DashboardModule { }



