import { Component, Input, OnChanges } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Project } from '../class/project';
import { Utils } from '../class/utils';
import * as firebase from 'firebase/app';
import 'firebase/storage';
declare const $: any;

@Component({
  selector: 'general-info',
  templateUrl: './general-information.component.html',
  styleUrls: ['./general-information.component.css']
})

export class GeneralInformationComponent implements OnChanges{
  @Input() project: Project;
  displayGeneralInfo = true;
  changeValue: boolean[];
  load = false;
  private numberField = 9;

  constructor(private database: AngularFireDatabase){}

  ngOnChanges(): void {
    this.changeValue = [];
    for(let i = 0; i  < this.numberField; i++) {
      this.changeValue[i] = false;
    }
  }

  commit(field: string, val, index: number): void {
    this.changeValue[index] = false;
    this.database.object('/project-informations/' + this.project.id + '/' + field)
      .set(val);
  }

  commitEnter(key: string, field: string, val, index: number): void {
    if(key === 'Enter') {
      this.commit(field, val, index);
    }
  }

  openFileLoader(id): void {
    $('#' + id).click();
  }

  editPicture(e, field: string): void {
    console.log(e);
    console.log(field);
    const file: File = e.target.files[0];
    const randomName = Utils.generateRandomString();
    this.project[field] = randomName;
    this.load = true;
    firebase.storage().ref('profilePictureChampionDashboard/' + randomName).put(file).then(() => {
      this.database.object('project-informations/' + this.project.id + '/' + field)
        .set(randomName);
      firebase.storage().ref('profilePictureChampionDashboard/' + this.project[field]).getDownloadURL()
        .then(data => {
          this.project[field] = data;
          this.load = false;
        });
    });
  }
}



