import { Component, Input, OnChanges, ViewChild, ElementRef } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Observable } from "rxjs";
import { Project } from '../class/project';
import { Utils } from '../class/utils';
import {isUndefined} from "util";


@Component({
  selector: 'map',
  templateUrl: './map.component.html',
  styleUrls: ['./map.component.css']
})

export class MapComponent implements OnChanges {
	@Input() project: Project;
  @ViewChild('listData') listData: ElementRef;
  lat: number;
  lng: number;
  zoom: number;
  dateStart: string;
  dateEnd: string;
  chooseUser: boolean[];
  chooseActivity: boolean[];
  listActivity: string[];
  sessions: any[];
  sessionMap: any[];
  nbValByRequest: number = 200;
  nbValToDisplay: number = 100;
  finish: boolean = false;
  lastSession;
  loadVal: boolean = false;
  heightChildren: number = 38 + 5; //+5 margin-bottom
  replayActif: boolean[];

	constructor(private database: AngularFireDatabase){}

	ngOnChanges(){
	  this.sessions = [];
    this.sessionMap = [];
    this.listActivity = ['Identifications', 'Enrollments', 'Verifications'];
    this.chooseActivity = [true, true, true];
    this.chooseUser = [];
    for(let i = 0; i < this.project.listUsers.length; i++) {
      this.chooseUser.push(true);
    }
    this.replayActif = [false, false, false, false, false];
    this.dateStart = Utils.transformTimestampToDate(Date.now() - (7 * 24 * 3600 * 1000));
    this.dateEnd = Utils.transformTimestampToDate(Date.now());
    this.loadVal = true;
    this.getData(Utils.transformDateToTimestamp(this.dateEnd) + (24 * 3600 * 1000), Utils.transformDateToTimestamp(this.dateStart),
      this.project.listUsers, Utils.convertActivity(this.chooseActivity), true, 0, false, true);
	}

  getData(dateEnd: number, dateStart: number, users: string[], activity: string[], reset: boolean, nbPush: number, skipLast: boolean, changeValue: boolean): void {
    let url: string;
    if(this.project.name === 'Global')
      url  = '/all-sessions';
    else
      url = '/project-sessions/' + this.project.id;
    let nb: number = this.nbValByRequest;
    if(skipLast)
      nb ++;
    if(reset)
      this.sessions = [];
    this.database.list(url, {
      query: {
        orderByChild: 'serverSessionStartTime',
        endAt: dateEnd,
        startAt: dateStart,
        limitToLast: nb
      }
    }).subscribe(data => {
      let tmpSession = [];
      let nbGet = 0;
      this.lastSession = {};
      let getFirst = false;
      data.forEach(session => {
        nbGet ++;
        if(!getFirst) {
          this.lastSession = session;
          getFirst = true;
        }
        if(activity.includes(session.callout) && (this.project.name === 'Global' || users.includes(session.userId) || users.includes('ID-' + session.userId)) &&
           !isUndefined(session['latitude']) && !isUndefined(session['longitude'])) {
          if(nbGet !== nb || !skipLast) {
            nbPush++;
            session['iconUrl'] = this.putIconUrl(session['callout']);
            session['callout'] = this.reduceActivity(session['callout']);
            session['latitude'] = parseInt(session['latitude']);
            session['longitude'] = parseInt(session['longitude']);
            tmpSession.push(session);
          }
        }
      });
      tmpSession.reverse();
      tmpSession.forEach(it => this.sessions.push(it));
      console.log(nbGet);
      if(nbGet < nb){
        this.changeSessionMap(0, Math.round(this.listData.nativeElement.clientHeight / this.heightChildren));
        this.finish = true;
        this.loadVal = false;
      }
      else {
        if(nbPush < this.nbValToDisplay){
          console.log("ok1");
          this.getData(this.lastSession['serverSessionStartTime'], dateStart, users, activity, false, nbPush, true, changeValue);
        }
        else{
          if(changeValue) {
            this.changeSessionMap(0, Math.round(this.listData.nativeElement.clientHeight / this.heightChildren));
          }
          this.loadVal = false;
        }
      }
    });
  }

	applyFilter(): void {
	  this.finish = false;
    let users: string[] = [];
    for(let i = 0; i < this.chooseUser.length; i++)
      if(this.chooseUser[i])
        users.push(this.project.listUsers[i]);
    this.loadVal = true;
    this.getData(Utils.transformDateToTimestamp(this.dateEnd) + (24 * 3600 * 1000), Utils.transformDateToTimestamp(this.dateStart),
      users,Utils.convertActivity(this.chooseActivity), true, 0, false, true);
  }

  loadNewValue(e: any): void {
	  if(!this.isReplayLaunch()) {
      const heightParent = e.target.clientHeight;
      const nbDisplay = Math.round(heightParent / this.heightChildren);
      const scrollTop = e.target.scrollTop;
      const nbScroll = Math.round(scrollTop / this.heightChildren);
      this.changeSessionMap(nbScroll, nbScroll + nbDisplay);
      if (nbScroll + nbDisplay >= this.sessions.length && !this.finish && !this.loadVal) {
        this.loadVal = true;
        let users: string[] = [];
        for (let i = 0; i < this.chooseUser.length; i++)
          if (this.chooseUser[i])
            users.push(this.project.listUsers[i]);
        this.getData(this.lastSession['serverSessionStartTime'], Utils.transformDateToTimestamp(this.dateStart),
          users, Utils.convertActivity(this.chooseActivity), false, 0, true, false);
      }
    }
  }

  changeSessionMap(start: number, end: number): void {
	  this.sessionMap = [];
    for(let i = start; i < end; i++)
      this.sessionMap.push(this.sessions[i])
    this.calculateGlobalZoom();
  }

  launchReplay(nbToReplay, index): void {
    if(!this.isReplayLaunch()) {
      this.replayActif[index] = true;
      let i = 0;
      let n = Math.min(nbToReplay, this.sessions.length);
      this.zoom = 7;
      let cpySession = this.sessions;
      this.sessions = [];
      let timer = Observable.timer(0, 700);
      let subscribe = timer.subscribe(t => {
        this.sessionMap = [];
        this.sessions = [];
        this.lat = cpySession[i]['latitude'];
        this.lng = cpySession[i]['longitude'];
        this.sessionMap.push(cpySession[i]);
        this.sessions.push(cpySession[i]);
        if (t == n - 1) {
          subscribe.unsubscribe();
          this.sessions = cpySession;
          this.replayActif[index] = false;
          this.changeSessionMap(0, Math.round(this.listData.nativeElement.clientHeight / this.heightChildren));
        }
        i++;
      });
    }
  }

  calculateGlobalZoom(): void{
    let maxLat: number = -1000;
    let maxLng: number = -1000;
    let minLat: number = 1000;
    let minLng: number = 1000;
    for(let i = 0; i <= this.sessionMap.length; i++){
      if(this.sessionMap[i] !== undefined){
        if(this.sessionMap[i].latitude > maxLat)
          maxLat = this.sessionMap[i].latitude;
        if(this.sessionMap[i].latitude < minLat)
          minLat = this.sessionMap[i].latitude;
        if(this.sessionMap[i].longitude > maxLng)
          maxLng = this.sessionMap[i].longitude;
        if(this.sessionMap[i].longitude < minLng)
          minLng = this.sessionMap[i].longitude;
      }
    }
    this.lng = (minLng + maxLng) / 2;
    this.lat = (maxLat + minLat) / 2;
    let max: number = Math.max(Math.abs(this.lng - minLng), Math.abs(this.lng - maxLng),
      Math.abs(this.lat - minLat), Math.abs(this.lat - maxLat));
    this.zoom = this.calculateZoom(max);
  }

  calculateZoom(max: number): number{
    if(max <= 0.001)
      return 15;
    else if(max <= 0.005)
      return 13;
    else if(max <= 0.02)
      return 12;
    else if(max <= 0.1)
      return 11;
    else if(max <= 0.5)
      return 7;
    else if(max <= 1)
      return 6;
    else if(max <= 2)
      return 5;
    else if(max <= 50)
      return 2;
    else
      return 1;
  }

  isReplayLaunch(): boolean{
	  for(let i = 0; i < this.replayActif.length; i++){
	    if(this.replayActif[i])
	      return true;
    }
	  return false;
  }
  reduceActivity(activty: string): string {
	  switch (activty) {
      case 'IDENTIFY':
        return 'ID';
      case 'REGISTER':
        return 'Enroll';
      case 'VERIFY':
        return 'Verif';
    }
  }

  putIconUrl(val: string): string{
	  const directory = "../../assets/";
    switch (val) {
      case "IDENTIFY":
        return directory + "iconIdentify.png";
      case "REGISTER":
        return directory + "iconRegister.png";
      case "VERIFY":
        return directory + "iconVerify.png";
      case "UPDATE":
        return directory + "iconUpdate.png";
    }
  }
}







