import { Component, Input, OnChanges}  from '@angular/core';
import { AngularFireDatabase }         from 'angularfire2/database';
import { FirebaseObjectObservable }    from 'angularfire2/database';
import { Project }                     from './../class/project';
import { Config }				       from './../class/config';

@Component({
  selector: 'change-config',
  templateUrl: './change-config.component.html',
  styleUrls: ['./change-config.component.css']
})

export class ChangeConfigComponent implements OnChanges {
	@Input() project: Project;
	configs: Config[];
	values: FirebaseObjectObservable<any>;
	thresholdQuality: Config = new Config();
	language: Config = new Config();
	syncSettings: Config = new Config();
	searchSettings: Config = new Config();
	nudgeMode: Config = new Config();
	load: boolean = false;
	valueChange: boolean = false;

	constructor(private database: AngularFireDatabase){}

	ngOnChanges(){
		this.configs = [];
		if(this.project !== undefined){
			this.configs = new Array();
			this.getDefinition();
		}
	}

	getDefinition(): void{
		this.database.list('/config/definition')
		.subscribe(data => {
			data.forEach(item =>{
				let newConfig = new Config();
				newConfig.key = item.$key;
				newConfig.value = item.default;
				newConfig.min = item.min;
				newConfig.max = item.max;
				newConfig.defaultVal = item.default;
				if(item.type != "seek" && item.type != "button"){
					newConfig.choices = [];
					this.database.list('/config/definition/' + newConfig.key+'/values')
					.subscribe(data => {
						data.forEach(item => {
							newConfig.choices.push(item.$value);
						})
						this.configs.push(newConfig);
					})
				}
				else{
					this.configs.push(newConfig);
				}
			})
			console.log(this.configs);
			this.getData();
		})
	}

	getData(): void{
		this.database.list('/config/projects/' + this.project.id)
		.subscribe(data => {
			data.forEach(item => {
				this.changeValue(item.$value, item.$key);
			})
			console.log(this.configs);
			this.putIndex();
		})
	}

	changeValue(newValue: string, key: string): void{
		this.configs.forEach(config => {
			if(config.key == key){
				config.value = newValue;
				config.defaultVal = newValue;
				return;
			}
		});
	}

	putIndex(): void{
		for(let i = 0; i < this.configs.length; i++){
			switch (this.configs[i].key) {
				case "thresholdQuality":
					this.thresholdQuality = this.configs[i];
					break;
				case "language":
					this.language = this.configs[i];
					break;
				case "syncSettings":
					this.syncSettings = this.configs[i];
					break;
				case "searchSettings":
					this.searchSettings = this.configs[i];
					break;
				case "nudgeMode":
					this.nudgeMode = this.configs[i];
					break;
			}
		}
	}

	changeCheck(component: Config, val: any): void{
		this.valueChange = true;
		component.value = val;
	}

	isDefault(defaultValue: string, val: any): boolean{
		return defaultValue == val;
	}

	changeSelect(e: any, component: Config): void{
		this.valueChange = true;
		component.value = e.target.value;
	}

	changeSeek(): void{
		this.valueChange = true;
	}

	cancel(): void{
		this.valueChange = false;
		this.configs.forEach(item => {
			item.value = item.defaultVal;
		})
	}

	onSubmit(): void{
		if(confirm("Are you sure to change the settings of the project " + this.project.name)){
			this.load = true;
			console.log(this.syncSettings);
			console.log(this.searchSettings);
			console.log(this.thresholdQuality);
			console.log(this.language);
			this.values = this.database.object('/config/projects/' + this.project.id);
			this.values.update({language: this.language.value,
								searchSettings: this.searchSettings.value,
								syncSettings: this.syncSettings.value,
								thresholdQuality: this.thresholdQuality.value})
			.then(data => this.load = false);
			console.log("submit");
		}
	}
	
}



