export class Utils{
  static listBlueError = ['LOW_BATTERY', 'BLUETOOTH_NOT_ENABLED', 'MULTIPLE_PAIRED_SCANNERS',
    'NOT_PAIRED', 'DISCONNECTED'];
  static listRedError = ['UNEXPECTED_ERROR'];
  static listYellowError = ['MISSING_MODULE_ID', 'INVALID_API_KEY', 'MISSING_USER_ID'];
  static listGreyError = ['GUID_NOT_FOUND_OFFLINE', 'GUID_NOT_FOUND_ONLINE', 'UNVERIFIED_API_KEY'];
  static generateRandomString(): string {
    let text = '';
    const possible = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789-_';

    for (let i = 0; i < 30; i++) {
      text += possible.charAt(Math.floor(Math.random() * possible.length));
    }
    return text;
  }

  static transformDateToTimestamp(date: string): number{
    const dateSplit = date.split('-');
    return new Date(dateSplit[1] + "/" + dateSplit[2] + "/" + dateSplit[0]).getTime();
  }

  static transformTimestampToDate(time: number): string {
    let dateString = '';
    let date: Date = new Date(time);
    date.setUTCHours(0);
    date.setUTCMinutes(0);
    date.setUTCSeconds(0);
    date.setUTCMilliseconds(0);
    dateString += date.getUTCFullYear() + "-";
    if((date.getUTCMonth() + 1) / 10 < 1)
      dateString += '0' + (date.getUTCMonth() + 1) + "-";
    else
      dateString += (date.getUTCMonth() + 1) + "-";
    if((date.getUTCDate()) / 10 < 1)
      dateString += '0' + date.getUTCDate();
    else
      dateString += date.getUTCDate();
    return dateString;
  }

  static getTodayDate(): string {
    return Utils.transformTimestampToDate(Date.now());
  }

  static computeDateArray(dateStart: string, dateEnd: string): string[] {
    let tab = [];
    const dateStartSplit = dateStart.split("-");
    let dateStartTimeStamp: number = new Date(dateStartSplit[1]+"/"+dateStartSplit[2]+"/"+dateStartSplit[0]).getTime();
    const dateEndSplit = dateEnd.split("-");
    let dateEndTimeStamp = new Date(dateEndSplit[1]+"/"+dateEndSplit[2]+"/"+dateEndSplit[0]).getTime();
    while(dateStartTimeStamp <= dateEndTimeStamp){
      dateStartTimeStamp += 24 * 3600 * 1000;
      tab.push(Utils.transformTimestampToDate(dateStartTimeStamp));
    }

    return tab;
  }

  static convertActivity(tab: boolean[]): string[] {
    let activity: string[] = [];
    if(tab[0])
      activity.push('IDENTIFY');
    if(tab[1])
      activity.push('REGISTER');
    if(tab[2])
      activity.push('VERIFY');
    return activity;
  }

  static transformTab(data: any[]): any {
    let newData: any = {};
    for(let i = 0; i < data.length; i ++)
      newData[data[i].$key] = data[i];
    return newData;
  }
}




