import { Component } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { Router } from '@angular/router';
import { Http, Headers, RequestOptions } from '@angular/http';
import * as firebase from 'firebase/app';

@Component({
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})

export class LoginComponent {
  load = false;
  url = 'https://simprints-dev.appspot.com/key';
  apiKey = '708ba617e56449ec8163e820c390a91d0046c5f9dd344ae7ac9fbb9b0ae80d8c';
  token: string;
  img: string;
  constructor(private afAuth: AngularFireAuth,
              private router: Router, private http: Http) {}

  login() {
    // firebase.storage().ref('FullSizeRenderpg').getDownloadURL().then(data => {
    //   console.log(data);
    //   this.img = data;
    // }).catch(() => {
    // });
    this.load = true;
    this.afAuth.auth.signInWithPopup(new firebase.auth.GoogleAuthProvider())
      .then(() => {
        this.load = false;
        return this.router.navigate(['/dashboard']);
        // this.checkUser();
      });
  }


  checkUser() {
    if (this.authorizeUser()) {
      this.getToken();
    }
    else {
      alert('bad user');
    }
  }

  authorizeUser(): boolean {
    const regex = new RegExp(`^.+@simprints\.com`);
    return regex.test(this.afAuth.auth.currentUser.email);
  }

  getToken(): void {
    const headers = new Headers({ 'Content-Type': 'application/json' });
    headers.append('Accept', 'application/json');
    const options = new RequestOptions({ headers: headers });
    this.http.post(this.url, JSON.stringify({apiKey: this.apiKey}), options).map(response => {
      return response.json();
    }).subscribe(data => {
      this.token = data.token;
      this.afAuth.auth.signInWithCustomToken(data.token).then(() => {
        this.load = false;
        return this.router.navigate(['/dashboard']);
      });
    });
  }
}



