import { Component, Input, OnChanges } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Angular2Csv } from 'angular2-csv/Angular2-csv';
import { DatePipe, DecimalPipe } from '@angular/common';
import { Project } from '../class/project';
import { Utils } from '../class/utils';

@Component({
  selector: 'table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.css'],
  providers: [DatePipe, DecimalPipe]
})

export class TableComponent implements OnChanges {
	@Input() project: Project;
  dateStart: string;
  dateEnd: string;
  chooseUser: boolean[];
  chooseActivity: boolean[];
  listActivity: string[];
  sessions: any[];
  heightRow: number = 30;
  nbValByRequest: number = 200;
  nbValToDisplay: number = 100;
  finish: boolean = false;
  lastSession;
  loadVal: boolean = false;

  constructor(private database: AngularFireDatabase, private datePipe: DatePipe, private decimalPipe: DecimalPipe) {}

	ngOnChanges(){
    this.listActivity = ['Identifications', 'Enrollments', 'Verifications'];
    this.chooseActivity = [true, true, true];
    this.finish = false;
    this.chooseUser = [];
    for(let i = 0; i < this.project.listUsers.length; i++) {
      this.chooseUser.push(true);
    }
    this.dateStart = Utils.transformTimestampToDate(Date.now() - (7 * 24 * 3600 * 1000));
    this.dateEnd = Utils.transformTimestampToDate(Date.now());
    this.loadVal = true;
    this.getData(Utils.transformDateToTimestamp(this.dateEnd) + (24 * 3600 * 1000), Utils.transformDateToTimestamp(this.dateStart),
      this.project.listUsers, Utils.convertActivity(this.chooseActivity), true, 0, false);
	}

	getData(dateEnd: number, dateStart: number, users: string[], activity: string[], reset: boolean, nbPush: number, skipLast: boolean): void {
	  let url: string;
	  if(this.project.name === 'Global')
	    url  = '/all-sessions';
	  else
	    url = '/project-sessions/' + this.project.id;
	  let nb: number = this.nbValByRequest;
	  if(skipLast)
	    nb ++;
	  if(reset)
      this.sessions = [];
	  this.database.list(url, {
      query: {
        orderByChild: 'serverSessionStartTime',
        endAt: dateEnd,
        startAt: dateStart,
        limitToLast: nb
      }
    }).subscribe(data => {
        let tmpSession = [];
        let nbGet = 0;
        this.lastSession = {};
        let getFirst = false;
        data.forEach(session => {
          nbGet ++;
          if(!getFirst) {
            this.lastSession = session;
            getFirst = true;
          }
          if(activity.includes(session.callout) && (this.project.name === 'Global' || (users.includes(session.userId) || users.includes('ID-' + session.userId)))) {
            if(nbGet !== nb || !skipLast) {
              let newSession = {};
              newSession['Time'] = this.datePipe.transform(session['serverSessionStartTime']);
              newSession['User'] = session['userId'];
              newSession['Activity'] = session['callout'];
              newSession['GPS'] = this.decimalPipe.transform(session['latitude'], '1.1-5') + ' ' +
                                  this.decimalPipe.transform(session['longitude'], '1.1-5');
              if(session['callout'] === "IDENTIFY") {
                let n: string = this.decimalPipe.transform(((session['serverSessionEndTime'] - session['serverMatchStartTime']) / 1000), '1.1-3');
                newSession['Match Speed'] = '' + n + 's';
              }
              else
                newSession['Match Speed'] = '';
              newSession['Version'] = session['appVersion'] + ', ' + session['hardwareVersion'];
              nbPush++;
              tmpSession.push(newSession);
            }
          }
        });
        tmpSession.reverse();
        tmpSession.forEach(it => this.sessions.push(it));
        if(nbGet < nb){
          this.finish = true;
          this.loadVal = false;

        }
        else {
          if(nbPush < this.nbValToDisplay){
            this.getData(this.lastSession['serverSessionStartTime'], dateStart, users, activity, false, nbPush, true);
          }
          else{
            this.loadVal = false;
          }
        }
      });
  }

	applyFilter(){
    this.finish = false;
	  let users: string[] = [];
	  for(let i = 0; i < this.chooseUser.length; i++)
	    if(this.chooseUser[i])
	      users.push(this.project.listUsers[i]);
    this.loadVal = true;
    this.getData(Utils.transformDateToTimestamp(this.dateEnd) + (24 * 3600 * 1000), Utils.transformDateToTimestamp(this.dateStart),
      users,Utils.convertActivity(this.chooseActivity), true, 0, false);
  }

  loadNewValue(e: any){
	  const heightParent = e.target.clientHeight;
    const nbDisplay = Math.ceil(heightParent / this.heightRow);
    const scrollTop = e.target.scrollTop;
    const nbScroll = Math.ceil(scrollTop / this.heightRow);
    if(nbDisplay + nbScroll >= this.sessions.length && !this.finish && !this.loadVal){
      this.loadVal = true;
      let users: string[] = [];
      for(let i = 0; i < this.chooseUser.length; i++)
        if(this.chooseUser[i])
          users.push(this.project.listUsers[i]);
      this.getData(this.lastSession['serverSessionStartTime'], Utils.transformDateToTimestamp(this.dateStart),
        users, Utils.convertActivity(this.chooseActivity), false, 0, true);
    }
  }

  exportCSV(): void {
    const options = {
      fieldSeparator: ';',
      quoteStrings: '"',
      decimalseparator: '.',
      showLabels: true,
      useBom: true
    };
    new Angular2Csv(this.sessions, 'test', options);
  }
}



