export class Project {
  countStat;
  errorStat;
  infoStat;
  timeStat;
  id: string;
  name: string;
  background: string;
  backgroundUrl: string;
  users;
  listUsers: string[];
  projectManager: string;
  dateStart: number;
  dateEnd: number;
  contract: string;
  details: string;
  hqChampionName: string;
  hqChampionLastDate: number;
  fieldChampionName: string;
  fieldChampionLastDate: string;
  hqChampionPicture: string;
  fieldChampionPicture: string;

  constructor(){
    this.countStat = {};
    this.errorStat = {};
    this.infoStat = {};
    this.timeStat = {};
    this.users = {};
    this.name = '';
  }
}



