import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { Project } from '../class/project';
import { Router } from '@angular/router';
import { AngularFireAuth } from 'angularfire2/auth';
import { AuthService } from '../auth.service';
import { Utils } from '../class/utils';
import * as firebase from 'firebase/app';
import 'firebase/storage';
import { isUndefined } from 'util';
declare const $: any;

@Component({
  selector: 'dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})

export class DashboardComponent implements OnInit {
  projects: Project[];
  globalProject: Project = new Project();
  selectedProject: Project = new Project();
  selectOption: string;
  admin = false;
  imgBackground: string;
  imgBackgroundDefault: string;
  imgProfileDefault: string;

  constructor(private database: AngularFireDatabase, public afAuth: AngularFireAuth,
              private authService: AuthService, private router: Router) {}

  ngOnInit() {
    console.log('init');
    firebase.storage().ref('backgroundDashboard/defaultBackground.png').getDownloadURL().then(data => {
      this.imgBackgroundDefault = data;
      this.imgBackground = data;
      this.globalProject.backgroundUrl = data;
    });
    firebase.storage().ref('profilePictureChampionDashboard/defaultProfile.png').getDownloadURL().then(data => {
      this.imgProfileDefault = data;
      console.log(data);
    });
    this.globalProject.name = 'Global';
    this.database.list('/project-informations').subscribe(data => {
      this.projects = [];
      data.forEach(item => {
        if (item.projectName !== undefined) {
          this.cpyInformation(item);
        }
      });
      this.projects.sort((a: Project, b: Project) => {
        if (a.name < b.name) {
          return -1;
        }
        else if (a.name > b.name) {
          return 1;
        }
        else {
          return 0;
        }
      });
      this.globalProject.listUsers = [];
      this.projects.forEach(it => this.globalProject.listUsers.push(it.name));
    });
  }

  openFileLoader(id): void {
    $('#' + id).click();
  }

  editPicture(e: any): void {
    const file: File = e.target.files[0];
    const randomName = Utils.generateRandomString();
    this.selectedProject.background = randomName;
    firebase.storage().ref('backgroundDashboard/' + randomName).put(file).then(() => {
      this.database.object('project-informations/' + this.selectedProject.id + '/background')
        .set(randomName);
      firebase.storage().ref('backgroundDashboard/' + this.selectedProject.background).getDownloadURL()
        .then(data => {
          this.selectedProject.backgroundUrl = data;
        });
    });
  }

  cpyInformation(info): void {
    const newProject: Project = new Project();
    newProject.id = info.$key;
    newProject.name = info.projectName;
    newProject.background = info.background;
    newProject.projectManager = info.projectManager;
    newProject.dateStart = info.dateStart;
    newProject.dateEnd = info.dateEnd;
    newProject.contract = info.contract;
    newProject.details = info.details;
    newProject.hqChampionName = info.hqChampionName;
    newProject.hqChampionLastDate = info.hqChampionLastDate;
    newProject.fieldChampionName = info.fieldChampionName;
    newProject.fieldChampionLastDate = info.fieldChampionLastDate;
    if(!isUndefined(info.hqChampionPicture)){
      firebase.storage().ref('profilePictureChampionDashboard/' + info.hqChampionPicture).getDownloadURL()
        .then(data => {
          newProject.hqChampionPicture = data;
        });
    }
    else {
      newProject.hqChampionPicture = this.imgProfileDefault;
    }
    if(!isUndefined(info.fieldChampionPicture)){
      firebase.storage().ref('profilePictureChampionDashboard/' + info.fieldChampionPicture).getDownloadURL()
        .then(data => {
          newProject.fieldChampionPicture = data;
        });
    }
    else {
      newProject.fieldChampionPicture = this.imgProfileDefault;
    }
    if (!isUndefined(newProject.background)) {
      firebase.storage().ref('backgroundDashboard/' + newProject.background).getDownloadURL()
        .then(data => {
          newProject.backgroundUrl = data;
        });
    }
    else {
      newProject.backgroundUrl = this.imgBackgroundDefault;
    }
    this.projects.push(newProject);
  }

  selectProject(project: Project): void {
    if(isUndefined(project.backgroundUrl))
      project.backgroundUrl = this.imgBackgroundDefault;
    if(isUndefined(project.fieldChampionPicture))
      project.fieldChampionPicture = this.imgProfileDefault;
    if(isUndefined(project.hqChampionPicture))
      project.hqChampionPicture = this.imgProfileDefault;
    this.selectedProject = project;
    this.selectedProject.users = [];
    this.selectOption = 'Chart';
    console.log(project);
  }

  seeGlobal(): void {
    this.selectProject(this.globalProject);
  }

  logout(): any {
    this.authService.isLoggedIn = null;
    this.afAuth.auth.signOut();
    return this.router.navigate(['/login']);
  }
}



