import { Component }                       from '@angular/core';
import { Http, Headers, RequestOptions }   from '@angular/http';
import { AuthService }                     from './../auth.service';
import { ProjectCreate }                   from './../class/projectCreate';
import { AngularFireDatabase }             from 'angularfire2/database';


@Component({
	selector: 'create-project',
	templateUrl: './create-project.component.html',
  	styleUrls: ['./create-project.component.css']	
})

export class CreateProjectComponent {
	project: ProjectCreate = new ProjectCreate();
	test: boolean = true;
	load: boolean = false;
	key: string = "708ba617e56449ec8163e820c390a91d0046c5f9dd344ae7ac9fbb9b0ae80d8c";
	url: string = "https://create-project-dot-simprints-dev.appspot.com/createApi";
	constructor(private http: Http, private authService: AuthService,
		        private database: AngularFireDatabase){}

	onSubmit(){
		this.project.test = this.test;
		let headers = new Headers({ 'Content-Type': 'application/json' });
		let options = new RequestOptions({ headers: headers });
		let data = {
				data:{
				Info: this.project.informations,
				Issuer: this.authService.login,
				Test: this.project.test
			},
			key: this.key
		}
		this.load = true;
    	this.http.post(this.url, JSON.stringify(data), options)
    	.map(response => {
      		return response.json();
    	}).subscribe(data => {
      		this.database.object("/project-informations/" + data.apiKey)
      		.set({projectManager: this.project.projectManager,
      			   projectName: this.project.name,
      			   contract: this.project.contract,
      			   dateStart: this.project.dateStart,
      			   dateEnd: this.project.dateEnd})
      		.then(() => {
      			this.load = false;
      		});
    	});
	}
}


