import { Component, Input, OnChanges, ViewChild } from '@angular/core';
import { AngularFireDatabase } from 'angularfire2/database';
import { BaseChartDirective } from 'ng2-charts/ng2-charts';
import { DecimalPipe } from '@angular/common';
import { Observable } from 'rxjs';
import { Project } from '../class/project';
import { Utils } from '../class/utils';
import { isUndefined } from "util";

@Component({
  selector: 'project-performance',
  templateUrl: './project-performance.component.html',
  styleUrls: ['./project-performance.component.css'],
  providers: [DecimalPipe]
})

export class ProjectPerformanceComponent implements OnChanges{
  @Input() project: Project;
  @ViewChild(BaseChartDirective) chart: BaseChartDirective;
  displayProjectPerformance = true;
  dateStart: string;
  dateEnd: string;
  chooseUser: string[] = [];
  dailyUser: number;
  monthlyUser: number;
  matchSpeed: number;
  accuracy: number = 0;
  displayChart = false;

  constructor(private database: AngularFireDatabase, private decimalPipe: DecimalPipe) {}

  ngOnChanges() {
    this.chooseUser[0] = 'All';
    this.initVal();
  }

  initVal(): void {
    this.dateStart = Utils.transformTimestampToDate(Date.now() - (7 * 24 * 3600 * 1000));
    this.dateEnd = Utils.transformTimestampToDate(Date.now());
    this.loadData();
  }

  loadData(): void {
    let url: string;
    if(this.project.name !== 'Global') {
      if (this.chooseUser[0] === 'All')
        url = `dashboard/project/${this.project.id}/project-data`;
      else
        url = `dashboard/project/${this.project.id}/user-data/users/ID-${this.chooseUser}`;
    }
    else
      url = 'dashboard/global';
    Observable.forkJoin(
      this.database.list(`${url}/IDENTIFY-time-stats/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`dashboard/project/${this.project.id}/project-data/daily-active-users/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1),
      this.database.list(`${url}/accuracy/dates`, {
        query:{
          orderByKey: true,
          startAt: this.dateStart,
          endAt: this.dateEnd
        }
      }).take(1)
    ).subscribe(data => {
      this.calculateValue(data);
      this.createGraph(data);
    });
  }

  calculateValue(tab: any[]): void {
    if(tab[0].length > 0) {
      this.matchSpeed = ((tab[0][tab[0].length - 1]['matching-time']['averageToDate'] * tab[0][tab[0].length - 1]['matching-time']['numberToDate'] -
        tab[0][0]['matching-time']['averageToDate'] * tab[0][0]['matching-time']['numberToDate']) /
        (tab[0][tab[0].length - 1]['matching-time']['numberToDate'] -
          tab[0][0]['matching-time']['numberToDate'])) / 1000;
      if(isNaN(this.matchSpeed))
        this.matchSpeed = 0;
    }
    else
      this.matchSpeed = 0;
    this.dailyUser = this.countDailyUser(Utils.transformTab(tab[1]), this.dateEnd);
    this.monthlyUser = this.countPeriodUser(Utils.computeDateArray(this.dateStart, this.dateEnd), Utils.transformTab(tab[1]));
    this.accuracy = this.calculateAccuracy(Utils.transformTab(tab[2]));
  }

  calculateAccuracy(data: any): number {
    let val: number = 0;
    for(let i = 0; i < 20; i++){
      if(!isUndefined(data[this.dateEnd])) {
        if(!isUndefined(data[this.dateStart]))
          val += (data[this.dateEnd][`${i}ToDate`] || 0) - (data[this.dateStart][`${i}ToDate`] || 0);
        else
          val += (data[this.dateEnd][`${i}ToDate`] || 0);
      }
    }
    let total: number;
    if(!isUndefined(data[this.dateEnd])){
      if(!isUndefined(data[this.dateStart]))
        total = (data[this.dateEnd][`totalToDate`] || 0) - (data[this.dateStart][`totalToDate`] || 0);
      else
        total = (data[this.dateEnd][`totalToDate`] || 0);
      if(total === 0)
        total = 1;
      return (val / total) * 100;
    }
    else
      return val;
  }

  countPeriodUser(dateArray: string[], data: any): number {
    let user = {};
    let nb = 0;
    for (let i = 0; i < dateArray.length; i++) {
      if (!isUndefined(data[dateArray[i]])) {
        for (let key in data[dateArray[i]]) {
          user[key] = true;
        }
      }
    }
    for (let key in user) {
      if(this.chooseUser[0] === 'All' || this.chooseUser[0] === key)
        nb++;
    }
    return nb;
  }

  countDailyUser(data: any, date: string): number {
    if(isUndefined(data[date]))
      return 0;
    else{
      let nb = 0;
      for(let key in data[date]) {
        if(this.chooseUser[0] === 'All' || this.chooseUser[0] === key)
          nb++;
      }
      return nb;
    }
  }

  createGraph(data: any[]): void {
    let dateArray = Utils.computeDateArray(this.dateStart, this.dateEnd);
    this.lineChartData = [];
    this.lineChartLabelsAccuracy = Array.from(Array(10), (e, i) => i + 1);
    this.lineChartDataAccuracy = [];
    this.lineChartDataAccuracy.push({data: this.computeNewValueAccuracyGraph(Utils.transformTab(data[2])), label: 'Accuracy'});
    let dataUser = this.computeNewValueUserGraph(dateArray, Utils.transformTab(data[1]));
    let dataAverage = this.computeNewValueAverageGraph(dateArray, Utils.transformTab(data[0]));
    this.lineChartData.push({data: dataUser, label: 'Daily User', borderColor: 'rgb(39, 170, 225)',
      backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)', yAxisID: 'y-axis-0'});
    this.lineChartData.push({data: dataAverage, label: 'Matching Speed', borderColor: 'rgb(231, 95, 156)',
      backgroundColor: 'rgba(255,255,255,0)', pointBackgroundColor: 'rgba(148,159,177,1)',
      pointBorderColor: '#fff', pointHoverBackgroundColor: '#fff', pointHoverBorderColor: 'rgba(148,159,177,0.8)', yAxisID: 'y-axis-1'});
    setTimeout(() => {
      if (this.chart && this.chart.chart && this.chart.chart.config) {
        this.chart.chart.config.data.labels = dateArray;
        this.chart.chart.config.data.datasets = this.lineChartData;
        this.chart.chart.update();
      }
    });
    this.displayChart = true;
  }

  computeNewValueAccuracyGraph(data: any): number[] {
    let graph = [];
    let total: number;
    if(!isUndefined(data[this.dateEnd])){
      if(!isUndefined(data[this.dateStart]))
        total = (data[this.dateEnd][`totalToDate`] || 0) - (data[this.dateStart][`totalToDate`] || 0);
      else
        total = (data[this.dateEnd][`totalToDate`] || 0);
      if(total === 0)
        total = 1;
    }
    let val = 0;
    for(let i = 0; i < 10; i++){
      if(!isUndefined(data[this.dateEnd])) {
        if(!isUndefined(data[this.dateStart]))
          val += (data[this.dateEnd][`${i}ToDate`] || 0) - (data[this.dateStart][`${i}ToDate`] || 0);
        else
          val += data[this.dateEnd][`${i}ToDate`] || 0;
        graph.push(this.decimalPipe.transform((val / total) * 100, '1.0-2'));
      }
    }
    return graph;
  }

  computeNewValueAverageGraph(dateArray: string[], data: any): number[]{
    let graph: number[] = [];
    for(let i = 0; i < dateArray.length; i++){
      if(isUndefined(data[dateArray[i]]))
        graph.push(0);
      else
        graph.push(data[dateArray[i]]['matching-time']['averageThisDay'] / 1000);
    }
    return graph;
  }

  computeNewValueUserGraph(dateArray: string[], data: any): number[]{
    let graph: number[] = [];
    for (let i = 0; i < dateArray.length; i++) {
      graph.push(this.countDailyUser(data, dateArray[i]));
    }
    return graph;
  }

  applyFilter(): void {
    this.loadData();
  }

  public lineChartLegend:boolean = true;
  public lineChartType:string = 'line';
  public lineChartData:Array<any>;
  public lineChartLabels:Array<any>;
  public lineChartOptions:any = {
    responsive: true,
    maintainAspectRatio: false,
    scales: {
      xAxes: [{
        stacked: true
      }],
      yAxes: [{
        type: 'linear',
        position: "left",
        id: "y-axis-0",
        scaleLabel: {
          display: true,
          labelString: 'Number of user'
        }
      }, {
        type: 'linear',
        position: "right",
        id: "y-axis-1",
        scaleLabel: {
          display: true,
          labelString: 'Matching speed'
        }
      }]
    }
  };
  public lineChartLegendAccuracy:boolean = true;
  public lineChartTypeAccuracy:string = 'bar';
  public lineChartDataAccuracy:Array<any>;
  public lineChartLabelsAccuracy:Array<any>;
  public lineChartOptionsAccuracy:any = {
    responsive: true,
    maintainAspectRatio: false
  };
  public lineChartColorAccuracy: Array<any> = [{
    backgroundColor: 'rgba(116, 77, 155, 0.5)',
    hoverBackgroundColor: 'rgba(116, 77, 155, 1)',
    borderColor: 'rgb(116, 77, 155)',
    borderWidth: 1
  }];
}



