import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { AppComponent } from './app.component';
import { AppRoutingModule } from './app-routing.module';
import { DashboardModule } from './dashboard/dashboard.module';
import { ChartsModule } from 'ng2-charts/ng2-charts';
import { Ng2SmartTableModule } from 'ng2-smart-table';
import { LoginComponent } from './login.component';
import { AgmCoreModule } from '@agm/core';
import { Ng2PageScrollModule } from 'ng2-page-scroll';
import { NglModule } from 'ng-lightning/ng-lightning';
import { DragulaModule } from 'ng2-dragula/ng2-dragula';
import { HttpModule } from '@angular/http';
import { CookieService } from 'angular2-cookie/services/cookies.service';
import { JwtHelper } from 'ng2-jwt';

export const firebaseConfig = {
  apiKey: 'AIzaSyAC8BvlIotworGYsiQyGHOoeBBirYQ46J4',
  authDomain: 'simprints-dev.firebaseapp.com',
  databaseURL: 'https://simprints-dev.firebaseio.com',
  projectId: 'simprints-dev',
  storageBucket: 'simprints-dev.appspot.com',
  messagingSenderId: '79630518081'
};

@NgModule({
  declarations: [
    AppComponent,
    LoginComponent
  ],
  imports: [
    BrowserModule,
    HttpModule,
    DashboardModule,
    ChartsModule,
    FormsModule,
    Ng2SmartTableModule,
    AppRoutingModule,
    Ng2PageScrollModule.forRoot(),
    AgmCoreModule.forRoot({
      apiKey: 'AIzaSyAC8BvlIotworGYsiQyGHOoeBBirYQ46J4'
    }),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    NglModule,
    DragulaModule
  ],
  providers: [
    CookieService,
    JwtHelper
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



